import * as React from "react";
import { styled, alpha } from "@mui/material/styles";

import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";

import Autocomplete from "@mui/material/Autocomplete";
import { Button, TextField } from "@mui/material";

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledTextField = styled(TextField)(({ theme }) => ({
  width: "300px !important", // Adjust this width as needed
  "& .MuiInputBase-input": {
    color: "white",
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(2)}) !important`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.up("sm")]: {
      width: "12ch !important",
      "&:focus": {
        width: "20ch !important",
      },
    },
  },
}));

const CinemanimaHeading = styled("h1")`
  font-family: "Indie Flower", cursive;
  font-size: 2rem;
  color: #ffcc00;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin: 0;
  padding: 0;
`;
const StyledToolbar = styled(Toolbar)`
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 10px;
  #logo {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: 50px;
      /* Add styles for larger screens */
    }
  }

  #logo:hover,
  img:hover,
  #text:hover {
    cursor: pointer;
  }

  @media (max-width: 600px) {
    /* Adjust styles for smaller screens */
    #logo {
      justify-content: start !important;
      flex-direction: column;
      align-items: start;
      img {
        height:15px !important; /* Adjust the height for smaller screens */
      }
      #text {
       display: block;
       font-size: 15px;
      }
    }
  }
`;
const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  width: '100%',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));
function Header() {
  const move = function () {
    window.location.href = "/";
  };
  const handleOptionClick = (option) => {
    console.log(option);
  };
  const options = [
    { label: "Option 1", imageUrl: "/path/to/image1.png" },
    { label: "Option 2", imageUrl: "/path/to/image2.png" },
    { label: "Option 3", imageUrl: "/path/to/image3.png" },
  ];

  return (
    <Box
      sx={{ flexGrow: 1 }}
      style={{
        zIndex: "5",
        backdropFilter: "blur(2px)",
        position: "sticky",
        top: "0px",
      }}
    >
      <AppBar
        position="sticky"
        style={{
          boxShadow: "none",
          background: "transparent",
          position: "sticky",
        }}
      >
        <StyledToolbar>
          <div id="logo" onClick={move}>
            <img
              src={`${process.env.PUBLIC_URL}/logo_.png`}
              alt="Logo"
              style={{ height: "40px", marginRight: "5px" }}
            />
            <CinemanimaHeading id="text">Cinemanima</CinemanimaHeading>
          </div>
          <div style={{display:'flex',columnGap:'10px'}}>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase  
              placeholder="Search…"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
          <Button variant="contained" onClick={handleOptionClick}>
            Go
          </Button>
          </div>
        </StyledToolbar>
      </AppBar>
    </Box>
  );
}

export default Header;
